<template>
  <div>
    <div class="filter-container">
      <el-row :gutter="10">
        <el-col :sm="24" :md="12">
          <el-button class="filter-item" @click="handleCreate" type="primary" icon="el-icon-plus">{{$t('table.add')}}</el-button>
          <el-button class="filter-item" type="danger" :disabled="multipleSelection.length <= 0"
            v-if="multipleSelection.length>0"
            @click="handleDelete(multipleSelection)"
            icon="el-icon-delete">{{$t('table.delete')}} ({{multipleSelection.length}} item)</el-button>
        </el-col>
        <el-col :sm="24" :md="12">
          <el-input placeholder="Search" v-model="urlParams.s" class="input-with-select" @keydown.enter.native="getProductCategories">
            <el-button slot="append" icon="el-icon-search" @click="getProductCategories"></el-button>
          </el-input>
        </el-col>
      </el-row>
    </div>

    <div class="table-container">
      <el-table :data="productCategories" v-loading="loading" element-loading-text="Memuat data..." @selection-change="handleSelectionChange" border fit highlight-current-row style="width: 100%">
        <el-table-column type="selection" width="55" style="margin:0 auto;"></el-table-column>

        <el-table-column prop="name" label="Name" sortable>
        </el-table-column>

        <el-table-column align="center" :label="$t('table.actions')" width="230" class-name="small-padding fixed-width">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="handleUpdate(scope.row)" icon="el-icon-edit">{{$t('table.edit')}}</el-button>
            <el-button type="danger" size="mini" @click="handleDelete([scope.row])" icon="el-icon-delete" style="width:80px;" >{{$t('table.delete')}}</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- Pagination -->
      <div class="pagination">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pagination.currentPage"
          :page-sizes="pagination.sizes"
          :page-size="pagination.pageSize"
          layout="sizes, total, prev, pager, next"
          :total="pagination.total">
        </el-pagination>
      </div>
    </div>

    <el-dialog :title="textMap[formDialog.status]" :visible.sync="formDialog.visible" :width="formDialog.dialog.width">
      <el-form :model="productCategoriesForm" :rules="productCategoriesFormRules" ref="productCategoriesForm" :label-position="formDialog.dialog.labelPosition" label-width="100px" @submit.native.prevent>

        <el-form-item label="Name" prop="name">
          <el-input type="text" v-model="productCategoriesForm.name" placeholder="name" @keydown.enter.native="handleEnterForm"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button @click="formDialog.visible = false">{{$t('table.cancel')}}</el-button>
          <el-button v-if="formDialog.status==='create'" :loading="loading" type="primary" @click="createData">{{$t('table.confirm')}}</el-button>
          <el-button v-else type="primary" @click="updateData">{{$t('table.confirm')}}</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title="Delete Confirmation"
      :visible.sync="deleteDialog.visible"
      :width="formDialog.dialog.width"
      center>
      <span>Are you sure want to delete?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog.visible = false">Cancel</el-button>
        <el-button type="primary" @click="deleteData">Yes</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import { dialog } from "@/responsive";

export default {
  data() {
    return {
      productCategories: [],
      loading: false,
      textMap: {
        update: "Edit",
        create: "Create"
      },

      // form
      productCategoriesForm: {
        name: ""
      },
      productCategoriesFormRules: {
        name: [{ required: true }]
      },

      // edit
      formDialog: {
        visible: false,
        status: "",
        selectedId: undefined,
        dialog: dialog.md
      },

      // delete
      deleteDialog: {
        visible: false,
        dialog: dialog.md30
      },
      deleteProductCategoryIds: [],

      // table
      multipleSelection: [],

      // Pagination
      pagination: {
        total: 0,
        pageSize: 10,
        currentPage: 1,
        sizes: [5, 10, 20, 30, 40, 50, 100]
      },

      // Get
      urlParams: {
        s: "",
        l: 10,
        p: 1,
        oby: "created_at", // order_by (category_id|name|created_at)
        sort: "desc"
      },

      // Responsive
      windowWidth: window.innerWidth
    };
  },

  methods: {
    // Create
    handleCreate() {
      this.resetForm();
      this.formDialog = {
        ...this.formDialog,
        status: "create",
        visible: true
      };
      this.$nextTick(() => {
        this.$refs["productCategoriesForm"].clearValidate();
      });

      // Responsive
      this.makeDialogResponsive();
    },
    createData() {
      this.$refs.productCategoriesForm.validate(valid => {
        if (valid) {
          this.loading = true;

          this.$store
            .dispatch("AddProductCategory", this.productCategoriesForm)
            .then(res => {
              // refetch productCategories data
              this.getProductCategories();

              this.$message.success(res.data.messages[0]);
              this.formDialog.visible = false;
              this.loading = false;
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              errorMsg.forEach(msg => {
                this.$message.error(msg);
              });
              this.loading = false;
            });
        }
      });
    },

    // Update
    handleUpdate(row) {
      // this.productCategoriesForm = Object.assign({}, row); // copy obj
      this.productCategoriesForm = {
        name: row.name
      };
      this.formDialog = {
        status: "update",
        visible: true,
        selectedId: row.category_id
      };
      this.$nextTick(() => {
        this.$refs["productCategoriesForm"].clearValidate();
      });

      // Responsive
      this.makeDialogResponsive();
    },
    updateData() {
      this.$refs.productCategoriesForm.validate(valid => {
        if (valid) {
          this.loading = true;

          // add productCategorie Id
          this.productCategoriesForm.categoryId = this.formDialog.selectedId;

          this.$store
            .dispatch("UpdateProductCategory", this.productCategoriesForm)
            .then(res => {
              // refetch productCategories data
              this.getProductCategories();

              this.loading = false;
              this.formDialog.visible = false;
              this.$message.success(res.data.messages[0]);
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              errorMsg.forEach(msg => {
                this.$message.error(msg);
              });
              this.loading = false;
            });
        }
      });
    },

    // Delete
    handleDelete(productCategories) {
      this.deleteDialog.visible = true;
      this.deleteProductCategoryIds = productCategories.map(
        productCategorie => {
          return productCategorie.category_id;
        }
      );

      // Responsive
      this.makeDialogResponsive(dialog.sm, dialog.md30);
    },
    deleteData() {
      this.deleteDialog.visible = false;
      this.$store
        .dispatch("DeleteProductCategory", this.deleteProductCategoryIds)
        .then(res => {
          // refetch productCategories data
          this.getProductCategories();

          this.$message.success(res.data.messages[0]);
          this.deleteDialog.visible = false;
          this.deleteProductCategoryIds = [];
        })
        .catch(error => {
          const errorMsg = error.response.data.messages;
          errorMsg.forEach(msg => {
            this.$message.error(msg);
          });
        });
    },

    // Form
    resetForm() {
      // this.$refs[formName].resetFields();
      this.productCategoriesForm = {
        name: ""
      };
    },
    handleEnterForm() {
      if (this.formDialog.status === "create") {
        this.createData();
      } else {
        this.updateData();
      }
    },

    // Table
    handleSelectionChange(productCategories) {
      this.multipleSelection = productCategories;
    },

    // Pagination
    handleSizeChange(pageSize) {
      this.pagination = {
        ...this.pagination,
        pageSize
      };
      this.urlParams = {
        ...this.urlParams,
        l: pageSize
      };

      this.getProductCategories();
    },
    handleCurrentChange(currentPage) {
      this.pagination = {
        ...this.pagination,
        currentPage
      };
      this.urlParams = {
        ...this.urlParams,
        p: currentPage
      };
      this.getProductCategories();
    },

    // Get
    getProductCategories() {
      this.loading = true;
      this.$store
        .dispatch("FetchProductCategories", this.urlParams)
        .then(res => {
          const data = res.data;
          const messages = data.messages;
          const status_code = data.status_code;

          this.productCategories = data.data.categories;
          this.loading = false;

          // set pagination
          this.pagination = {
            ...this.pagination,
            total: data.data.total
          };
        });
    },

    // Responsive
    makeDialogResponsive(sm = dialog.sm, md = dialog.md) {
      if (this.windowWidth < 990) {
        this.formDialog = {
          ...this.formDialog,
          dialog: sm
        };
      } else {
        this.formDialog = {
          ...this.formDialog,
          dialog: md
        };
      }
    }
  },

  watch: {
    windowWidth(newWidth, oldWidth) {
      this.makeDialogResponsive();
    }
  },

  created() {
    this.getProductCategories();
  },

  mounted() {
    let that = this;
    this.$nextTick(function() {
      window.addEventListener("resize", function(e) {
        that.windowWidth = window.innerWidth;
      });
    });
  }
};
</script>

<style scoped>

</style>



